
.pinned{
    display: flex;
    flex-direction: column;
}

.svg-inline--fa {
    font-size: 100px;
}

.shake {
    animation: shake 0.15s; 
    animation-iteration-count: 2; 
}

@keyframes shake {
    0% { transform: translate(0px, 0px); }
    50% { transform: translate(4px, 0px); }
    100% { transform: translate(-4px, 0px); }
}